.row_gc {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  /* margin-right: 25px;
    margin-left: 25px; */
  justify-content: center;
  -webkit-box-pack: center !important;

  
}

.col_md_12 {
  position: relative;
  width: 100%;
  min-height: 1px;
  /* padding-right: 15px;
    padding-left: 15px; */
  -webkit-box-flex: 0;
  -ms-flex: 0 0 100%;
  flex: 0 0 100%;
  max-width: 100%;
  
}

@media (min-width: 768px) {
  .col_md_6 {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 50%;
    flex: 0 0 50%;
    max-width: 50%;
  }
}

@media (max-width: 768px) {
  .col_md_6 {
    width: 100%;
    position: relative;
    min-height: 1px;
  }
}

@media (max-width: 991.98px) {
  .counter_wrap {
    margin-bottom: 40px !important;
  }
}

.col_lg_3 {
  position: relative;
  width: 100%;
  min-height: 1px;
  /* padding-right: 15px;
    padding-left: 15px; */
